.page {
  max-width: 1305px;
  width: 100%;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  color: #FFFFFF;
  padding: 0;
  margin: 0 auto;
}
.page__container {
  background: linear-gradient(153.11deg, #36603E 35.82%, #1E262C 107.75%);
  background-repeat: no-repeat;
  border-radius: 12px;
  display: flex;
  flex-direction: column;
  gap: 25.95px;
}
.page__title {
  padding: 0 20px;
  margin: 0 0 31.95px;
  font-family: Inter, Arial, sans-serif;
  font-size: 41.45px;
  font-weight: 600;
  line-height: 50.17px;
  text-align: center;
}
@media screen and (max-width: 815px) {
  .page__title {
    font-size: 35px;
    line-height: 41px;
  }
}
@media screen and (max-width: 425px) {
  .page__title {
    font-size: 25px;
    line-height: 30px;
  }
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  padding: 34.55px 48.36px;
}
@media screen and (max-width: 815px) {
  .header {
    padding: 25px 20px;
  }
}
.header__container {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
@media screen and (max-width: 815px) {
  .header__container {
    display: none;
    position: absolute;
    flex-direction: column;
    gap: 20px;
    top: 65px;
    right: 0;
    left: 0;
    align-items: center;
    justify-content: center;
    background: linear-gradient(153.11deg, #36603E 75%, #1E262C 150%);
    backdrop-filter: blur(30px);
    z-index: 10;
  }
  .header__container_type_active {
    display: flex;
  }
}
.header__burger-container {
  display: none;
}
@media screen and (max-width: 815px) {
  .header__burger-container {
    display: flex;
    width: 25px;
    height: 22px;
    position: relative;
  }
  .header__burger-container.active .header__burger-span:nth-child(1) {
    transform: translateY(0) rotate(45deg);
  }
  .header__burger-container.active .header__burger-span:nth-child(2) {
    opacity: 0;
  }
  .header__burger-container.active .header__burger-span:nth-child(3) {
    transform: translateY(0) rotate(-45deg);
  }
}
@media screen and (max-width: 815px) {
  .header__burger-span {
    position: absolute;
    padding: 0;
    margin: 0;
    display: inline-block;
    width: 100%;
    height: 2px;
    background-color: #FFFFFF;
    top: 50%;
    cursor: pointer;
    transition: 0.3s;
  }
  .header__burger-span:nth-child(1) {
    transform: translateY(10px);
  }
  .header__burger-span:nth-child(3) {
    transform: translateY(-10px);
  }
}
.header__list {
  display: flex;
  gap: 48.36px;
  list-style: none;
  padding: 0;
  margin: 0;
}
@media screen and (max-width: 1015px) {
  .header__list {
    gap: 35px;
  }
}
@media screen and (max-width: 815px) {
  .header__list {
    flex-direction: column;
  }
}
.header__link, .header__logo-link {
  padding: 0;
  margin: 0;
  font-family: Inter, Arial, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 15px;
  color: #FFFFFF;
  text-decoration: none;
  opacity: 0.8;
  transition: 0.3s;
}
.header__link:hover, .header__logo-link:hover {
  opacity: 1;
  color: #BC4749;
}
.header__link_active, .header__logo-link_active {
  font-weight: 600;
  opacity: 1;
}
.header__logo-link {
  display: flex;
  align-items: center;
  opacity: 1;
  margin-right: 87.23px;
  width: 100px;
}
@media screen and (max-width: 1015px) {
  .header__logo-link {
    margin-right: 50px;
  }
}
.header__logo-link:hover {
  color: #FFFFFF;
}
.header__logo-name {
  text-decoration: line-through;
  font-size: 11.45px;
  font-weight: 700;
  line-height: 14.65px;
  margin: 0 0 0 3.61px;
  padding: 0;
  opacity: 1;
}
.header__logo-icon {
  width: 23.49px;
  height: 15.66px;
  object-fit: cover;
}

.auth__singup {
  padding: 12px 34.5px;
  border-radius: 7px;
  border: 0.86px solid #D66567;
  opacity: 1;
  background-color: #BC4749;
  box-sizing: border-box;
  font-size: 12px;
  font-weight: 500;
  line-height: 15.36px;
  margin-left: 13.82px;
}
.auth__singup:hover {
  color: #FFFFFF;
  box-shadow: 0 0 15px red;
}
@media screen and (max-width: 815px) {
  .auth {
    margin: 30px 0;
  }
}

.main {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-image: url(../../images/backgronud-img.png);
  background-repeat: no-repeat;
  background-position: top;
  margin: 0 0 34.55px;
}

.tariffs {
  display: flex;
  min-height: 629px;
  height: 100%;
  align-items: flex-end;
  margin: 0 20px 60.28px;
}
@media screen and (max-width: 1023px) {
  .tariffs {
    align-items: center;
    justify-content: center;
    gap: 20px;
    flex-wrap: wrap;
  }
}
.tariffs__second {
  align-self: flex-start;
  z-index: 2 !important;
  background-color: #1D252B !important;
  scale: 1.055;
}
@media screen and (max-width: 1023px) {
  .tariffs__second {
    scale: 1;
    align-self: center;
  }
}
.tariffs__second-btn {
  background-color: #BC4749 !important;
  border: 0.86px solid #D66567 !important;
}
.tariffs__second-btn:hover {
  box-shadow: 0 0 15px red;
}
.tariffs__second-btn-arrow {
  width: 16px;
  height: 16px;
  object-fit: cover;
  margin-left: 9.46px;
}
.tariffs__second-description {
  border-bottom: 0px !important;
}

.tariff {
  background-color: #6A994E;
  border-radius: 11.35px;
  max-width: 333.84px;
  width: 100%;
  min-height: 592px;
  height: 100%;
  display: flex;
  flex-direction: column;
  box-shadow: 0px 3.78px 94.62px 0px rgba(0, 0, 0, 0.25);
  margin: 0;
  padding: 37.85px 0 28px;
  box-sizing: border-box;
  animation: drop-in 3000ms ease 5000ms backwards;
}
.tariff:nth-child(1) {
  animation-delay: 1s;
}
.tariff:nth-child(2) {
  animation-delay: 1.5s;
}
.tariff:nth-child(3) {
  animation-delay: 2s;
}
@keyframes drop-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.tariff__description {
  padding: 0 60px 28.04px;
  border-bottom: 0.95px solid rgba(255, 255, 255, 0.1);
  margin: 0 0 21.95px;
}
@media screen and (max-width: 1085px) {
  .tariff__description {
    padding-left: 45px;
    padding-right: 45px;
  }
}
.tariff__description-text {
  padding: 0;
  margin: 0 0 15.14px;
  font-family: Inter, Arial, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 16.8px;
  color: #FFFFFF;
  opacity: 0.8;
}
.tariff__price-container {
  display: flex;
  padding: 0 0 10.64px;
  margin: 0 0 10.64px;
  border-bottom: 0.95px solid rgba(255, 255, 255, 0.1);
}
.tariff__title {
  padding: 0;
  margin: 0 0 15.14px;
  font-family: Inter, Arial, sans-serif;
  font-size: 16px;
  font-weight: 600;
  line-height: 19.36px;
  color: #FFFFFF;
}
.tariff__price-currency {
  padding: 0;
  font-family: Inter, Arial, sans-serif;
  font-size: 11.35px;
  font-weight: 500;
  line-height: 14.54px;
  cursor: pointer;
  transition: 0.3s;
}
.tariff__price-currency:hover {
  color: #BC4749;
}
.tariff__price-value {
  padding: 0;
  margin: 0;
  font-family: Inter, Arial, sans-serif;
  font-size: 37.85px;
  font-weight: 800;
  line-height: 48.45px;
}
.tariff__price-period {
  padding: 0;
  margin: 0 0 10px 5px;
  font-family: Inter, Arial, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 14.52px;
  opacity: 0.7;
  align-self: end;
  cursor: pointer;
  transition: 0.3s;
}
.tariff__price-period:hover {
  color: #BC4749;
  opacity: 1;
}
.tariff__features {
  padding: 0 60px 0;
}
@media screen and (max-width: 1085px) {
  .tariff__features {
    padding-left: 45px;
    padding-right: 45px;
  }
}
.tariff__features-heading {
  margin: 0 0 10px;
  padding: 0;
  display: block;
  font-family: Inter, Arial, sans-serif;
  font-size: 15.14px;
  font-weight: 600;
  line-height: 19.38px;
}
.tariff__plan {
  margin: 0 0 24px;
  padding: 0;
  display: block;
  font-family: Inter, Arial, sans-serif;
  font-size: 13.25px;
  font-weight: 500;
  line-height: 16.96px;
}
.tariff__features-list {
  list-style-image: url(../../images/list-marker.png);
  margin: 0 0 0 8px;
  padding: 0;
}
.tariff__features-item {
  margin: 0 0 15.14px 7.57px;
  padding: 0;
  font-family: Inter, Arial, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 15.36px;
}
.tariff__features-item_old {
  opacity: 0.8;
  font-weight: 400;
}
.tariff__btn {
  align-self: center;
  display: flex;
  justify-content: center;
  margin-top: auto;
  background-color: transparent;
  outline: none;
  border: 0.86px solid #D0D0D0;
  border-radius: 7px;
  padding: 12px 64.5px;
  font-family: Inter, Arial, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 15.36px;
  color: #FFFFFF;
  box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  transition: 0.3s;
}
.tariff__btn:hover {
  background-color: #BC4749 !important;
  border: 0.86px solid #D66567 !important;
  box-shadow: 0 0 15px red;
}